:root {
  --clr-white: #ffffff;
  --clr-orange: #F18119;
  --clr-lightblack: #404040;
  --clr-orangeYellowGradient: linear-gradient(to left , #FF713B , #FFA51D);
}

:root {
  --ff-primary: "Source Sans Pro", sans-serif;
}

/*BreakPoints*/
/*Center Container*/
/*
Fonts for typography
@mixin setFont(
	$fontType,
	$fontSize,
	$fontWeight: "regular",
	$color: "default"
) {
	$fontKeyMap: map-get(
		$map: $fonts,
		$key: $fontType,
	);

	font-family: unquote(map-get($map: $fontKeyMap, $key: "type"));
	font-size: map-get($map: $fontKeyMap, $key: $fontSize);
	font-weight: map-get($map: $fontKeyMap, $key: $fontWeight);
	@if $color != "default" {
		color: $color;
	}

	@content;
}

*/
:root {
  --fs-heading1: 3.5rem;
  --fs-heading2: 1.75rem;
  --fs-complementary: 1.4rem;
  --fs-body: 1.125rem;
  --fs-small: 0.625rem;
}
@media (max-width: 63.9375em) {
  :root {
    --fs-heading1: 2.25rem ;
    --fs-heading2: 1.3125rem;
    --fs-complementary: 1.2rem;
    --fs-body: 1rem ;
  }
}

:root {
  --fw-regular: 400;
  --fw-bold: 900;
}

body {
  font-family: var(--ff-primary);
  font-size: var(--fs-body);
  text-align: center;
}

