/****** Elad Shechter's RESET *******/
/*** box sizing border-box for all elements ***/
*,
*::before,
*::after {
  box-sizing: border-box;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

button {
  background-color: transparent;
  color: inherit;
  border-width: 0;
  padding: 0;
  cursor: pointer;
}

figure {
  margin: 0;
}

input::-moz-focus-inner {
  border: 0;
  padding: 0;
  margin: 0;
}

ul,
ol,
dd {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
}

p {
  margin: 0;
}

cite {
  font-style: normal;
}

fieldset {
  border-width: 0;
  padding: 0;
  margin: 0;
}

.section__small {
  text-transform: uppercase;
  font-size: var(--fs-small);
}
.section__small--left {
  position: relative;
  color: var(--clr-orange);
}
.section__complementary {
  font-size: var(--fs-complementary);
  text-transform: uppercase;
  color: var(--clr-orange);
  position: relative;
  font-weight: var(--fw-bold);
  margin-top: 0.2em;
}
.section__heading1 {
  font-size: var(--fs-heading1);
  max-width: 17ch;
  margin-bottom: 0.5em;
  position: relative;
}
.section__heading2 {
  font-size: var(--fs-heading2);
  color: var(--clr-lightblack);
}

.bold {
  font-weight: var(--fw-bold);
}

.section {
  display: flex;
  min-height: 100vh;
  width: 50%;
  flex-direction: column;
  align-items: center;
}
.section__small--left {
  padding: 0.5em 0;
}
.section__small--left::before {
  content: "";
  position: absolute;
  top: 0;
  width: 100%;
  padding: 0.2em;
  background: var(--clr-orangeYellowGradient);
}
.section--left {
  background-color: black;
  margin-bottom: 1em;
  position: relative;
  color: #fff;
}
.section--left::before {
  content: "";
  position: absolute;
  inset: 0 0 0 0;
  background-image: url("https://images.unsplash.com/photo-1508615263227-c5d58c1e5821?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=675&q=80");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  opacity: 0.5;
}
.section__headers {
  margin-bottom: 3em;
}
.section__description {
  max-width: 60ch;
  line-height: 1.2;
  margin-bottom: 1em;
}
.section__credentials {
  width: 70%;
  display: flex;
  flex-direction: column;
}
.section__credentials * {
  margin-bottom: 1em;
}
.section__credentials__input {
  padding: 0.5em 0;
  text-align: center;
  border: 2px solid #a1a0a0;
  border-radius: 5px;
}
.section__credentials__submit {
  padding: 0.7em 0;
  background: var(--clr-orangeYellowGradient);
  color: white;
  border-radius: 5px;
}
.section__statements {
  margin-top: 0.5em;
}
.section__statements * {
  margin-bottom: 1em;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
  justify-content: center;
}
.container--headers {
  display: flex;
  flex-direction: column;
  padding: 0.5em;
}
.container--right {
  width: 80%;
  max-width: 550px;
}

.main {
  display: flex;
  height: 95vh;
}

@media (max-width: 63.9375em) {
  .main {
    flex-direction: column;
  }

  .section {
    min-height: 50vh;
    width: 100%;
  }

  .container--left {
    justify-content: space-around;
  }
}